.song-page {
	display: grid;
	grid-auto-flow: column;
}

.song-information,
#song-page-back-button {
	font-size: calc(0.1em + 1.2vw);
}

.song-cover-container {
	display: flex;
	justify-content: center;
	flex-direction: column;
	align-items: center;

}

#song-cover {
	width: calc(1em + 15vw);
}

#song-page-back-button {
	background-color: #487cce;
	margin: 10px 10px;
	padding: 0.2vw 1vw;
}

#back-button-container {
	display: flex;
	align-items: flex-end;
	justify-content: right;
}